import React from 'react'
import FirstImg from '../../images/2.jpg';
import SecondImg from '../../images/6.jpg';
import ThirdImg from '../../images/3.jpg';
import FourImg from '../../images/7.jpg';
import FiveImg from '../../images/8.jpg';
import Carousel from 'react-bootstrap/Carousel'
export default function ControlledCarousel() {
  const photos = [FirstImg, SecondImg, ThirdImg, FourImg, FiveImg];
  const renderImages = photos.map((item, index) => (
    <Carousel.Item key={item}>
      <img
        className="d-block w-100"
        src={item}
        alt={`${index+1} slide`}
      />
    </Carousel.Item>
  ));
    return (
      <div>
        <Carousel>
          {renderImages}
        </Carousel>
      </div>
    );
 
}
